"use client";

import {
	Calendar,
	Eye,
	FileText,
	Globe,
	Pencil,
	Plus,
	Trash2,
} from "lucide-react";
import { useEffect, useState } from "react";
import { toast } from "sonner";
import { PolarisButton } from "@/components/admin/polaris-button";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import {
	Dialog,
	DialogContent,
	DialogDescription,
	DialogFooter,
	DialogHeader,
	DialogTitle,
} from "@/components/ui/dialog";
import {
	Empty,
	EmptyContent,
	EmptyDescription,
	EmptyHeader,
	EmptyMedia,
	EmptyTitle,
} from "@/components/ui/empty";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";
import {
	Table,
	TableBody,
	TableCell,
	TableHead,
	TableHeader,
	TableRow,
} from "@/components/ui/table";
import { Textarea } from "@/components/ui/textarea";
import {
	createBlogPost,
	deleteBlogPost,
	getBlogPosts,
	getBlogStats,
	toggleBlogPostPublished,
	updateBlogPost,
} from "@/server/api/internal/blog";
import { getOrCreateDefaultLocale } from "@/server/api/internal/locales";

interface BlogPost {
	id: string;
	slug: string;
	featuredImage: string | null;
	author: string | null;
	isPublished: boolean;
	publishedAt: Date | null;
	title: string;
	excerpt: string;
	content: string;
	translations: {
		localeId: string;
		localeCode: string;
		localeName: string;
		title: string;
		excerpt: string | null;
		content: string;
	}[];
	createdAt: Date;
	updatedAt: Date;
}

interface BlogStats {
	total: number;
	published: number;
	drafts: number;
	recent: number;
}

export default function BlogPage() {
	const [posts, setPosts] = useState<BlogPost[]>([]);
	const [stats, setStats] = useState<BlogStats | null>(null);
	const [defaultLocaleId, setDefaultLocaleId] = useState<string | null>(null);
	const [loading, setLoading] = useState(true);
	const [viewDialogOpen, setViewDialogOpen] = useState(false);
	const [formDialogOpen, setFormDialogOpen] = useState(false);
	const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
	const [selectedPost, setSelectedPost] = useState<BlogPost | null>(null);
	const [formMode, setFormMode] = useState<"create" | "edit">("create");
	const [saving, setSaving] = useState(false);
	const [deleting, setDeleting] = useState(false);

	// Form state
	const [formData, setFormData] = useState({
		title: "",
		slug: "",
		excerpt: "",
		content: "",
		author: "",
		isPublished: false,
	});

	useEffect(() => {
		loadData();
	}, []);

	async function loadData() {
		try {
			setLoading(true);
			const [postsData, statsData] = await Promise.all([
				getBlogPosts(),
				getBlogStats(),
			]);
			setPosts(postsData);
			setStats(statsData);
		} catch (error) {
			toast.error("Failed to load blog posts");
		} finally {
			setLoading(false);
		}
	}

	const handleView = (post: BlogPost) => {
		setSelectedPost(post);
		setViewDialogOpen(true);
	};

	const handleEdit = (post: BlogPost) => {
		setSelectedPost(post);
		setFormMode("edit");
		setFormData({
			title: post.title,
			slug: post.slug,
			excerpt: post.excerpt,
			content: post.content,
			author: post.author ?? "",
			isPublished: post.isPublished,
		});
		setFormDialogOpen(true);
	};

	const handleCreate = () => {
		setSelectedPost(null);
		setFormMode("create");
		setFormData({
			title: "",
			slug: "",
			excerpt: "",
			content: "",
			author: "",
			isPublished: false,
		});
		setFormDialogOpen(true);
	};

	const handleDeleteClick = (post: BlogPost) => {
		setSelectedPost(post);
		setDeleteDialogOpen(true);
	};

	const handleTogglePublished = async (post: BlogPost) => {
		try {
			await toggleBlogPostPublished(post.id);
			toast.success(post.isPublished ? "Post unpublished" : "Post published");
			loadData();
		} catch (error) {
			toast.error("Failed to update post status");
		}
	};

	const generateSlug = (title: string) => {
		return title
			.toLowerCase()
			.replace(/[^a-z0-9\s-]/g, "")
			.replace(/\s+/g, "-")
			.replace(/-+/g, "-")
			.trim();
	};

	const handleSubmit = async () => {
		if (!formData.title || !formData.slug) {
			toast.error("Title and slug are required");
			return;
		}

		try {
			setSaving(true);

			// For now, we use a default locale ID - in production, this would come from settings
			const defaultLocaleId = "default"; // This should be fetched from tenant settings

			if (formMode === "create") {
				await createBlogPost({
					slug: formData.slug,
					author: formData.author || undefined,
					isPublished: formData.isPublished,
					translations: [
						{
							localeId: defaultLocaleId,
							title: formData.title,
							excerpt: formData.excerpt || undefined,
							content: formData.content,
						},
					],
				});
				toast.success("Blog post created");
			} else if (selectedPost) {
				await updateBlogPost(selectedPost.id, {
					slug: formData.slug,
					author: formData.author || undefined,
					isPublished: formData.isPublished,
					translations: [
						{
							localeId: defaultLocaleId,
							title: formData.title,
							excerpt: formData.excerpt || undefined,
							content: formData.content,
						},
					],
				});
				toast.success("Blog post updated");
			}

			setFormDialogOpen(false);
			loadData();
		} catch (error) {
			toast.error(
				error instanceof Error ? error.message : "Failed to save blog post"
			);
		} finally {
			setSaving(false);
		}
	};

	const handleDelete = async () => {
		if (!selectedPost) return;

		try {
			setDeleting(true);
			await deleteBlogPost(selectedPost.id);
			toast.success("Blog post deleted");
			setDeleteDialogOpen(false);
			loadData();
		} catch (error) {
			toast.error("Failed to delete blog post");
		} finally {
			setDeleting(false);
		}
	};

	const formatDate = (date: Date | null) => {
		if (!date) return "";
		return new Date(date).toLocaleDateString("bg-BG", {
			year: "numeric",
			month: "short",
			day: "numeric",
		});
	};

	if (loading) {
		return (
			<div className="flex items-center justify-center h-64">
				<div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary" />
			</div>
		);
	}

	return (
		<div className="space-y-6">
			<div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
				<div>
					<h1 className="text-3xl font-bold tracking-tight">Blog Posts</h1>
					<p className="text-muted-foreground mt-1">
						Create and manage blog content for your store
					</p>
				</div>
				<PolarisButton onClick={handleCreate}>
					<Plus className="h-4 w-4" />
					Create Post
				</PolarisButton>
			</div>

			<div className="grid gap-4 md:grid-cols-4">
				<Card className="p-4">
					<div className="flex items-center gap-2">
						<FileText className="h-4 w-4 text-muted-foreground" />
						<p className="text-sm text-muted-foreground">Total Posts</p>
					</div>
					<p className="text-2xl font-bold mt-1">{stats?.total ?? 0}</p>
				</Card>
				<Card className="p-4">
					<div className="flex items-center gap-2">
						<Globe className="h-4 w-4 text-muted-foreground" />
						<p className="text-sm text-muted-foreground">Published</p>
					</div>
					<p className="text-2xl font-bold mt-1">{stats?.published ?? 0}</p>
				</Card>
				<Card className="p-4">
					<div className="flex items-center gap-2">
						<Pencil className="h-4 w-4 text-muted-foreground" />
						<p className="text-sm text-muted-foreground">Drafts</p>
					</div>
					<p className="text-2xl font-bold mt-1">{stats?.drafts ?? 0}</p>
				</Card>
				<Card className="p-4">
					<div className="flex items-center gap-2">
						<Calendar className="h-4 w-4 text-muted-foreground" />
						<p className="text-sm text-muted-foreground">This Week</p>
					</div>
					<p className="text-2xl font-bold mt-1">{stats?.recent ?? 0}</p>
				</Card>
			</div>

			<Card>
				{posts.length === 0 ? (
					<Empty>
						<EmptyHeader>
							<EmptyMedia variant="icon">
								<FileText className="h-8 w-8" />
							</EmptyMedia>
							<EmptyTitle>No blog posts yet</EmptyTitle>
							<EmptyDescription>
								Start writing blog posts to engage with your customers and
								improve SEO
							</EmptyDescription>
						</EmptyHeader>
						<EmptyContent>
							<PolarisButton onClick={handleCreate}>
								<Plus className="h-4 w-4" />
								Create Post
							</PolarisButton>
						</EmptyContent>
					</Empty>
				) : (
					<Table>
						<TableHeader>
							<TableRow>
								<TableHead>Title</TableHead>
								<TableHead>Author</TableHead>
								<TableHead>Status</TableHead>
								<TableHead>Published</TableHead>
								<TableHead>Updated</TableHead>
								<TableHead className="text-right">Actions</TableHead>
							</TableRow>
						</TableHeader>
						<TableBody>
							{posts.map((post) => (
								<TableRow key={post.id}>
									<TableCell>
										<div>
											<p className="font-medium">{post.title}</p>
											<p className="text-sm text-muted-foreground">
												/{post.slug}
											</p>
										</div>
									</TableCell>
									<TableCell>{post.author ?? ""}</TableCell>
									<TableCell>
										<Badge
											variant={post.isPublished ? "default" : "secondary"}
											className="cursor-pointer"
											onClick={() => handleTogglePublished(post)}
										>
											{post.isPublished ? "Published" : "Draft"}
										</Badge>
									</TableCell>
									<TableCell>{formatDate(post.publishedAt)}</TableCell>
									<TableCell>{formatDate(post.updatedAt)}</TableCell>
									<TableCell className="text-right">
										<div className="flex justify-end gap-2">
											<Button
												variant="ghost"
												size="icon"
												onClick={() => handleView(post)}
											>
												<Eye className="h-4 w-4" />
											</Button>
											<Button
												variant="ghost"
												size="icon"
												onClick={() => handleEdit(post)}
											>
												<Pencil className="h-4 w-4" />
											</Button>
											<Button
												variant="ghost"
												size="icon"
												onClick={() => handleDeleteClick(post)}
											>
												<Trash2 className="h-4 w-4" />
											</Button>
										</div>
									</TableCell>
								</TableRow>
							))}
						</TableBody>
					</Table>
				)}
			</Card>

			{/* View Dialog */}
			<Dialog open={viewDialogOpen} onOpenChange={setViewDialogOpen}>
				<DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
					<DialogHeader>
						<DialogTitle>Blog Post Preview</DialogTitle>
						<DialogDescription>Preview how your post will look</DialogDescription>
					</DialogHeader>

					{selectedPost && (
						<div className="space-y-4">
							<div className="flex items-center gap-2">
								<Badge
									variant={selectedPost.isPublished ? "default" : "secondary"}
								>
									{selectedPost.isPublished ? "Published" : "Draft"}
								</Badge>
								<span className="text-sm text-muted-foreground">
									/{selectedPost.slug}
								</span>
							</div>

							<h2 className="text-2xl font-bold">{selectedPost.title}</h2>

							{selectedPost.author && (
								<p className="text-sm text-muted-foreground">
									By {selectedPost.author} {" "}
									{formatDate(selectedPost.publishedAt ?? selectedPost.createdAt)}
								</p>
							)}

							{selectedPost.excerpt && (
								<p className="text-lg text-muted-foreground italic">
									{selectedPost.excerpt}
								</p>
							)}

							<div className="border-t pt-4">
								<div
									className="prose prose-sm dark:prose-invert max-w-none"
									// biome-ignore lint/security/noDangerouslySetInnerHtml: Rich text content
									dangerouslySetInnerHTML={{ __html: selectedPost.content }}
								/>
							</div>
						</div>
					)}

					<DialogFooter>
						<Button variant="outline" onClick={() => setViewDialogOpen(false)}>
							Close
						</Button>
						<PolarisButton
							onClick={() => {
								setViewDialogOpen(false);
								if (selectedPost) handleEdit(selectedPost);
							}}
						>
							<Pencil className="h-4 w-4" />
							Edit Post
						</PolarisButton>
					</DialogFooter>
				</DialogContent>
			</Dialog>

			{/* Form Dialog */}
			<Dialog open={formDialogOpen} onOpenChange={setFormDialogOpen}>
				<DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
					<DialogHeader>
						<DialogTitle>
							{formMode === "create" ? "Create Blog Post" : "Edit Blog Post"}
						</DialogTitle>
						<DialogDescription>
							{formMode === "create"
								? "Write a new blog post for your store"
								: "Update your blog post content"}
						</DialogDescription>
					</DialogHeader>

					<div className="space-y-4">
						<div>
							<Label htmlFor="title">Title</Label>
							<Input
								id="title"
								value={formData.title}
								onChange={(e) => {
									setFormData({
										...formData,
										title: e.target.value,
										slug:
											formMode === "create"
												? generateSlug(e.target.value)
												: formData.slug,
									});
								}}
								placeholder="Enter post title"
							/>
						</div>

						<div>
							<Label htmlFor="slug">Slug</Label>
							<Input
								id="slug"
								value={formData.slug}
								onChange={(e) =>
									setFormData({ ...formData, slug: e.target.value })
								}
								placeholder="post-url-slug"
							/>
						</div>

						<div>
							<Label htmlFor="author">Author</Label>
							<Input
								id="author"
								value={formData.author}
								onChange={(e) =>
									setFormData({ ...formData, author: e.target.value })
								}
								placeholder="Author name"
							/>
						</div>

						<div>
							<Label htmlFor="excerpt">Excerpt</Label>
							<Textarea
								id="excerpt"
								value={formData.excerpt}
								onChange={(e) =>
									setFormData({ ...formData, excerpt: e.target.value })
								}
								placeholder="Brief summary of the post"
								rows={2}
							/>
						</div>

						<div>
							<Label htmlFor="content">Content</Label>
							<Textarea
								id="content"
								value={formData.content}
								onChange={(e) =>
									setFormData({ ...formData, content: e.target.value })
								}
								placeholder="Write your blog post content here..."
								rows={10}
							/>
						</div>

						<div className="flex items-center justify-between p-4 border rounded-lg">
							<div>
								<Label htmlFor="published">Publish immediately</Label>
								<p className="text-sm text-muted-foreground">
									Make this post visible on your store
								</p>
							</div>
							<Switch
								id="published"
								checked={formData.isPublished}
								onCheckedChange={(checked) =>
									setFormData({ ...formData, isPublished: checked })
								}
							/>
						</div>
					</div>

					<DialogFooter>
						<Button
							variant="outline"
							onClick={() => setFormDialogOpen(false)}
						>
							Cancel
						</Button>
						<PolarisButton onClick={handleSubmit} disabled={saving}>
							{saving
								? "Saving..."
								: formMode === "create"
									? "Create Post"
									: "Save Changes"}
						</PolarisButton>
					</DialogFooter>
				</DialogContent>
			</Dialog>

			{/* Delete Dialog */}
			<Dialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
				<DialogContent>
					<DialogHeader>
						<DialogTitle>Delete Blog Post</DialogTitle>
						<DialogDescription>
							Are you sure you want to delete &quot;{selectedPost?.title}&quot;?
							This action cannot be undone.
						</DialogDescription>
					</DialogHeader>

					<DialogFooter>
						<Button
							variant="outline"
							onClick={() => setDeleteDialogOpen(false)}
						>
							Cancel
						</Button>
						<Button
							variant="destructive"
							onClick={handleDelete}
							disabled={deleting}
						>
							{deleting ? "Deleting..." : "Delete Post"}
						</Button>
					</DialogFooter>
				</DialogContent>
			</Dialog>
		</div>
	);
}
